import React, { useState, useEffect, useRef } from 'react';
import ChatHistory from './ChatHistory';
import ChatExperts from './ChatExperts';

const ChatInterface = () => {
  const [message, setMessage] = useState('');
  const [textareaHeight, setTextareaHeight] = useState('52px');
  const textareaRef = useRef(null);
  const [chatMessages, setChatMessages] = useState([]);
  const maxTextareaHeight = 200;
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);
  const [isIconRotated, setIsIconRotated] = useState(false);
  const [isHistoryOpen, setIsHistoryOpen] = useState(false);
  const [isExpertsOpen, setIsExpertsOpen] = useState(false);
  const dropdownRef = useRef(null);
  const chatContainerRef = useRef(null);
  

  const getRandomBotResponse = () => {
    const responses = ["Hello!", "How can I assist you?", "Nice to meet you!"];
    const randomIndex = Math.floor(Math.random() * responses.length);
    return responses[randomIndex];
  };

  const toggleHistory = () => {
    setIsHistoryOpen(!isHistoryOpen);
    setIsExpertsOpen(false);
  };

  const toggleExperts = () => {
    setIsExpertsOpen(!isExpertsOpen);
    setIsHistoryOpen(false);
  };

  const toggleDropdown = () => {
    setIsDropdownOpen(!isDropdownOpen);
    setIsIconRotated(!isIconRotated);
  };

  const handleMessageChange = (e) => {
    setMessage(e.target.value);
    handleTextareaResize();
  };

  const handleTextareaResize = () => {
    const textarea = textareaRef.current;
    if (textarea) {
      textarea.style.height = '52px';
      const newHeight = Math.min(textarea.scrollHeight, maxTextareaHeight);
      textarea.style.height = `${newHeight}px`;
    }
  };

  const handleSendMessage = async () => {
    if (message.trim() !== '') {
      const userMessage = { text: message, user: 'user' };
      setChatMessages((prevChatMessages) => [...prevChatMessages, userMessage]);
      const botResponse = getRandomBotResponse();
      setChatMessages((prevChatMessages) => [
        ...prevChatMessages,
        { text: '', user: 'bot', isTyping: true },
      ]);
  
      const delay = (ms) => new Promise((resolve) => setTimeout(resolve, ms));
  
      for (let i = 0; i < botResponse.length; i++) {
        await delay(25);
        setChatMessages((prevChatMessages) => [
          ...prevChatMessages.slice(0, -1),
          { text: botResponse.slice(0, i + 1), user: 'bot', isTyping: true },
        ]);
      }
  
      setChatMessages((prevChatMessages) => [
        ...prevChatMessages.slice(0, -1),
        { text: botResponse, user: 'bot' },
      ]);
  
      setMessage('');
      handleTextareaResize();
  
      // Check if the device is desktop (screen width greater than 768 pixels, adjust as needed)
      if (window.innerWidth < 768 && textareaRef.current) {
        textareaRef.current.blur(); // Remove focus from the textarea
      }
    }
  };
  
  
  
  
  const handleClickOutside = (event) => {
    if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
      setIsDropdownOpen(false);
      setIsIconRotated(false);
    }
  };

  useEffect(() => {
    handleTextareaResize();
    const handleKeyDown = (event) => {
      if (event.key === 'Enter' && !event.shiftKey) {
        event.preventDefault();
        handleSendMessage();
      }
    };
  
    const textareaElement = textareaRef.current;
  
    if (textareaElement) {
      textareaElement.addEventListener('keydown', handleKeyDown);
    }
  
    return () => {
      if (textareaElement) {
        textareaElement.removeEventListener('keydown', handleKeyDown);
      }
    };
  }, [chatMessages]);
  
  

  useEffect(() => {
    if (message === '') {
      setTextareaHeight('52px');
    }
  }, [message]);

  useEffect(() => {
    window.addEventListener('click', handleClickOutside);

    return () => {
      window.removeEventListener('click', handleClickOutside);
    };
  }, []);

  useEffect(() => {
    if (chatContainerRef.current) {
      chatContainerRef.current.scrollTop = chatContainerRef.current.scrollHeight;
    }
  }, [chatMessages]);
  

  return (
    <div className="Chatcontainer-model">


<ChatHistory isOpen={isHistoryOpen} toggleOpen={toggleHistory} />

<div className='chat-display'>
  <div className="pick-model">
    <h4>Pick a Model</h4>
    <span
      className={`svg-chatbot-model ${isIconRotated ? 'svg-chatbot-model-rotate-icon' : ''}`}
      aria-hidden="true"
      onClick={toggleDropdown}
    >
      <svg width="8" height="16" viewBox="0 0 8 16">
        <path d="M0.772126 1.19065L0.153407 1.80934C0.00696973 1.95578 0.00696973 2.19322 0.153407 2.33969L5.80025 8L0.153407 13.6603C0.00696973 13.8067 0.00696973 14.0442 0.153407 14.1907L0.772126 14.8094C0.918563 14.9558 1.156 14.9558 1.30247 14.8094L7.84666 8.26519C7.99309 8.11875 7.99309 7.88131 7.84666 7.73484L1.30247 1.19065C1.156 1.04419 0.918563 1.04419 0.772126 1.19065Z"></path>
      </svg>
    </span>
    {/* Dropdown Box */}
    <div className={`chat-dropdown-model no-scroll  ${isDropdownOpen ? 'show' : ''}`}>
    <button className="chat-dropdown-model-button">
  <div className="d-flex align-items-center">
    <svg xmlns="http://www.w3.org/2000/svg" color='#212529' fill="none" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" height="25" width="25">
      <path strokeLinecap="round" strokeLinejoin="round" d="m3.75 13.5 10.5-11.25L12 10.5h8.25L9.75 21.75 12 13.5H3.75Z" />
    </svg>
    <div className='d-flex flex-column align-items-start'>
      <h5 className="m-0">GPT 3.5</h5>
      <h6 className="text-muted text-start m-0">Our most familiar model</h6>
    </div>
  </div>
  <div className="flex-grow-1 d-flex justify-content-end">
    <input type="radio" className="form-check-input listboxes" id="anyJobSuccess" name="jobSuccess" />
    <label className="form-check-label label-with-svg" htmlFor="anyJobSuccess"></label>
  </div>
</button>

<button className="chat-dropdown-model-button">
  <div className="d-flex align-items-center">
    <svg xmlns="http://www.w3.org/2000/svg" color='#212529' fill="none" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" height="25" width="25">
      <path strokeLinecap="round" strokeLinejoin="round" d="m3.75 13.5 10.5-11.25L12 10.5h8.25L9.75 21.75 12 13.5H3.75Z" />
    </svg>
    <div className='d-flex flex-column align-items-start'>
      <h5 className="m-0">Bard</h5>
      <h6 className="text-muted text-start m-0">Google's AI solution</h6>
    </div>
  </div>
  <div className="flex-grow-1 d-flex justify-content-end">
    <input type="radio" className="form-check-input listboxes" id="anyJobSuccess" name="jobSuccess" />
    <label className="form-check-label label-with-svg" htmlFor="anyJobSuccess"></label>
  </div>
</button>

<button className="chat-dropdown-model-button">
  <div className="d-flex align-items-center">
    <svg xmlns="http://www.w3.org/2000/svg" color='#212529' fill="none" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" height="25" width="25">
      <path strokeLinecap="round" strokeLinejoin="round" d="m3.75 13.5 10.5-11.25L12 10.5h8.25L9.75 21.75 12 13.5H3.75Z" />
    </svg>
    <div className='d-flex flex-column align-items-start'>
      <h5 className="m-0">Claude-2</h5>
      <h6 className="text-muted text-start m-0">Focused on ethics</h6>
    </div>
  </div>
  <div className="flex-grow-1 d-flex justify-content-end">
    <input type="radio" className="form-check-input listboxes" id="anyJobSuccess" name="jobSuccess" />
    <label className="form-check-label label-with-svg" htmlFor="anyJobSuccess"></label>
  </div>
</button>




    </div>
  </div>
  <div className='chat-column chat-bot-upper-section'>
  <div className="chat-box chat-bot-section"  ref={chatContainerRef}>
  {chatMessages.map((chatMessage, index) => (
  <div key={index} className={`chat-bubble-bot ${chatMessage.user === 'user' ? 'user-sent-bot' : 'bot'}`}>
    {chatMessage.user === 'bot' && (
      <div className="bot-message">
        <div className="bot-avatar">
          <img
            className="bot-avatar"
            src={`${process.env.PUBLIC_URL}/images/arts/TIO.webp`}
            alt="TIO-bot"
          />
        </div>
        <div className="bot-details">
          <div className="bot-name">TIO-bot</div>
          <div className="chat-text">{chatMessage.text}</div>
        </div>
      </div>
    )}
    {chatMessage.user !== 'bot' && <div className="chat-text">{chatMessage.text}</div>}
  </div>
))}
      </div>

      <div className="chatui-container">
        <div className="userui-input" >
        <textarea
  value={message}
  onChange={handleMessageChange}
  className="scroll-col textarea-chat"
  style={{ height: textareaHeight }} // Set the height dynamically
  ref={textareaRef}
  placeholder="Type a message..."
  autoComplete="off"
  inputMode="text"
  onKeyDown={(e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  }}
/>

          <button
          aria-label="Send Message"
          className="svg-container send-btn"
          onClick={handleSendMessage}
        >
          <i className="fas fa-paper-plane"></i>
        </button>
        </div>
        </div>
        </div>
        </div>
   
      <ChatExperts isOpen={isExpertsOpen} toggleOpen={toggleExperts} />
      </div>
  

  );
};

export default ChatInterface;
